import React from 'react';
import { geoMercator, geoPath } from 'd3-geo';
import './word-map.css';

const WIDTH = 1140;
const HEIGHT = 600;
const SCALE = 150;

const projection = () => geoMercator().scale(SCALE).translate([ WIDTH / 2, HEIGHT / 2 ]);

const Country = ({ datum, index, dataLength, isDone, name }) => {
  const classNames = isDone ? 'country visited' : 'country';
  console.log(typeof classNames);
  return (
    <path
      d={geoPath().projection(projection())(datum)}
      id={name}
      className={classNames}
      stroke="#FFFFFF"
      strokeWidth={0.5}
    />
  );
}

const Countries = ({ data = [], visited = {}}) => (
  <g className="countries">
    {data.map((d, i) => {
       const name = d.properties.name.split(' ').join('-').toLowerCase();
       const isDone = visited[name];
       return (
         <Country
           key={`country-${d.id || i}`}
           datum={d}
           index={i}
           name={name}
           dataLength={data.length}
           isDone={isDone} />
       )
    })}
  </g>
);

const WordMap = ({ countries = [], visited = {}}) => (
  <svg className="world-map-container" width={WIDTH} height={HEIGHT} viewBox={`0 0 ${WIDTH} ${HEIGHT}`}>
    <Countries data={countries} visited={visited} />
  </svg>
);

export default WordMap;
