import React, { useState, useEffect } from "react";
import { Link } from "gatsby";
import SEO from "../components/seo";
import { feature } from 'topojson-client';
import WordMap from '../components/word-map/word-map.component';
import wordData from '../../assets/countries-110m.json';
import Navigation from "../components/navigation/navigation.component";

const visitedCountries = [
  'mexico',
  'ukraine',
  'france',
  'germany',
  'united-states-of-america',
  'czech-republic',
  'poland',
  'netherlands',
].reduce((map, country) => ({...map, [country]: true }), {});

const IndexPage = () => {
  const [mapData, setMapData] = useState([]);

  useEffect(() => {
    const isEmpty = mapData.length === 0;
    if (isEmpty) {
      setMapData(() => feature(wordData, wordData.objects.countries).features);
    }
  }, [mapData]);

  return (
      <>
        <SEO title="My scratch map" />
        <section className="hero is-info is-bold is-fullheight">
          <div className="hero-head">
            <Navigation />
          </div>
          <div className="hero-body">
            <div className="is-hidden-touch" style={{margin: '0 auto'}}>
              <WordMap countries={mapData} visited={visitedCountries}/>
            </div>
            <div className="is-hidden-desktop has-text-centered">
              <h3 className="title">Not supported</h3>
              <h4 className="subtitle">Unfortunately the scratch map is not supported in mobile or tablet devices</h4>
              <button className="button is-light">
                <Link to="/">Go to home</Link>
              </button>
            </div>
          </div>
        </section>
      </>
  );
}

export default IndexPage;
